import React, { useRef, useEffect } from 'react';
import '../styles/canvas.css';

function FallingPixels() {
    
    // lets us access canvas DOM nodes as an object with initial value set to null
    // set as a ref on the canvas element
    const canvasRef = useRef(null);
    const myImage = new Image();
    myImage.src = 'data:image/png;base64,';
    
    useEffect(() => {

        // lets canvas continue to update after initial render
        const canvas = canvasRef.current;
        // gets us the current canvas element
        const ctx = canvas.getContext("2d");
        canvas.width = 482;
        canvas.height = 601;
        console.log(ctx)
        
        // src to be drawn, left at 0. top at 0, then dimensions
        ctx.drawImage(myImage, 0, 0, canvas.width, canvas.height);
        const pixels = ctx.getImageData(0, 0, canvas.width, canvas.height);

        let particlesArray = [];

        const numberOfParticles = 2500;

        let mappedImage = [];
        //getting color of each pixel on image
        for (let y = 0; y < canvas.height; y++) {
            let row = [];
            for (let x = 0; x < canvas.width; x++) {
                const red = pixels.data[(y * 4 * pixels.width) + (x * 4)];
                const green = pixels.data[(y * 4 * pixels.width) + (x * 4 + 1)];
                const blue = pixels.data[(y * 4 * pixels.width) + (x * 4 + 2)];
                const brightness = calculateRelativeBrightness(red, green, blue);
                const cell = [
                    brightness,
                ];
                row.push(cell);
            }
            mappedImage.push(row);
        }

        function calculateRelativeBrightness(red, green, blue) {
            return Math.sqrt(
                (red*red) * .299 + 
                (green*green) * .587 + 
                (blue*blue) + .114
            )/100;
        }


        class Particle {
            constructor(){
                this.x = Math.random() * canvas.width;
                this.y = 0;
                this.speed = 0;
                this.velocity = Math.random() * 3.5;
                this.size = Math.random() * 1.5 + 1;
                this.position1 = Math.floor(this.y);
                this.position2 = Math.floor(this.x);
            }
            update(){
                this.position1 = Math.floor(this.y);
                this.position2 = Math.floor(this.x);
                this.speed = mappedImage[this.position1][this.position2][0];
                let movement = (2.5 - this.speed) + this.velocity;

                this.y += movement;
                if (this.y >= canvas.height) {
                    this.y = 0;
                    this.x = Math.random() * canvas.width;
                }
            }
            draw(){
                ctx.beginPath();
                ctx.fillStyle = 'white';
                ctx.arc(this.x, this.y, this.size, 0, Math.PI * 2);
                ctx.fill();
            }
        }
        function init() {
            for (let i=0; i < numberOfParticles; i++) {
                particlesArray.push(new Particle);
            }
        }
        init();
        function animate() {
            ctx.globalAlpha = 0.05;
            ctx.fillStyle = 'rgb(0, 0, 0)';
            ctx.fillRect(0, 0, canvas.width, canvas.height);
            for (let i = 0; i < particlesArray.length; i++) {
                particlesArray[i].update();
                ctx.globalAlpha = particlesArray[i].speed * .5;
                particlesArray[i].draw();
            }
            requestAnimationFrame(animate)
        }
        animate();
    })

    return (
        <canvas ref={canvasRef} id="FallingPixels"></canvas>
    );
}

export default FallingPixels;